import React from 'react'
import {Link} from 'react-router-dom'

export default function Start() {
  return (
    <header className="App-header">
    <div style={{borderRadius:'40%',overflow:'hidden',margin:"10px"}}>
      <video
        src="videoJoy.mp4"
        preload="auto"
        autoPlay
        loop
        height="200vmin"
        playsInline
      />
    </div>
    <p style={{fontWeight:"bold"}}>
        
    </p>
    <Link 
      to='/reservation'
      style={{
            display: "inline-flex",
            alignItems: "center",
            justifyContent: "center",
            width: "40%",
            height:"50px",
            border:"0 solid transparent",
            borderRadius:"16px",
            backgroundColor:"#25282b",
            fontSize:"18px",
            fontWeight:"600",
            color:"white",
            textDecoration:"none",
            marginBottom:"15px"
          }}
    >
         
    </Link>
    <Link 
      to='/reservation21'
      style={{
            display: "inline-flex",
            alignItems: "center",
            justifyContent: "center",
            width: "40%",
            height:"50px",
            border:"0 solid transparent",
            borderRadius:"16px",
            backgroundColor:"#25282b",
            fontSize:"18px",
            fontWeight:"600",
            color:"white",
            textDecoration:"none"
          }}
    >
        
    </Link>
  </header>
  )
}
